.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo-link {
  width: 40px;
  height: 40px;
  display: block;
}

.menu {
  display: flex;
}

.menu__item {
  align-items: center;
  list-style: none;
  display: flex;
}

.menu__item:not(:first-child) {
  margin-left: 20px;
}

.menu__link {
  text-transform: uppercase;
  text-align: center;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active, .menu__link:hover {
  color: var(--color-blue);
}

.menu__link.is-active:after {
  content: "";
  background-color: var(--color-blue);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

ul {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  --color-blue: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

